export default {
  namespaced: true,
  state: {
    todos: JSON.parse(window.localStorage.getItem("todos_key") || "[]"),
    // todos: [],
  },

  mutations: {
    ADD_TODO(state, todo) {
      state.todos.unshift(todo);
    },
    DEL_TODO(state, index) {
      state.todos.splice(index, 1);
    },
    SEL_ALL(state, check) {
      state.todos.forEach((todo) => (todo.complete = check));
    },
    DEL_COM(state) {
      state.todos = state.todos.filter((todo) => !todo.complete);
    },
    RECEIVE_TODOS(state, todos) {
      state.todos = todos;
    },
  },

  actions: {
    AddTodo({ commit }, todo) {
      commit("ADD_TODO", todo);
    },
    delTodo({ commit }, index) {
      commit("DEL_TODO", index);
    },
    selectAllTodos({ commit }, check) {
      commit("SEL_ALL", check);
    },
    delCompleteTodo({ commit }) {
      commit("DEL_COM");
    },
    reqTodos({ commit }) {
      setTimeout(() => {
        const todos = JSON.parse(
          window.localStorage.getItem("todos_key") || "[]"
        );
        commit("RECEIVE_TODOS", todos);
      }, 2000);
    },
  },

  getters: {
    totalCount(state) {
      return state.todos.length;
    },
    completeCount(state) {
      return state.todos.reduce(
        (preTotal, todo) => preTotal + (todo.complete ? 1 : 0),
        0
      );
    },
    isAllSelect(state, getters) {
      return (
        getters.totalCount == getters.completeCount && getters.completeCount > 0
      );
    },
  },
};
