<template>
  <div>
    <div v-if="!repoUrl" v-loading="loading"></div>
    <div v-else>
      most star repo is
      <br />
      <a :href="repoUrl">{{ repoName }}</a>
    </div>
    <el-button type @click="isloading">刷新</el-button>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      loading: true,
      repoUrl: "",
      repoName: ""
    };
  },
  mounted() {
    //   ajax require
    // const url = "https://www.runoob.com/try/ajax/json_demo.json";
    const url = "https://api.github.com/search/repositories?q=vue&sort=stars";
    axios
      .get(url)
      .then(response => {
        const res = response.data;
        const mostRepo = res.items[0];
        this.repoUrl = mostRepo.html_url;
        this.repoName = mostRepo.full_name;
        this.loading = false;
      })
      .catch(function(error) {
        // 请求失败处理
        alert("Error");
        console.log(error);
      });
  },
  methods: {
    isloading() {
      const loading = this.$loading({
        lock: true,
        text: "Loading",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)"
      });
      setTimeout(() => {
        loading.close();
      }, 2000);
    }
  }
};
</script>

