export default {
  namespaced: true,
  // 初始化状态
  state: {
    count: 0,
  },
  mutations: {
    // 增加的 mutation
    INCREMENT(state) {
      state.count++;
    },
    DECREMENT(state) {
      state.count--;
    },
  },
  actions: {
    // 增加的 action
    increment({ commit }) {
      // 提交增加的 mutation
      commit("INCREMENT");
    },
    decrement({ commit }) {
      // 提交减少的 mutation
      commit("DECREMENT");
    },
    // 带条件的 action
    incrementIfOdd({ commit, state }) {
      if (state.count % 2 == 1) {
        commit("INCREMENT");
      }
    },
    // 异步 action
    incrementAsync({ commit }) {
      // 在action中可直接执行异步代码
      setTimeout(() => {
        commit("INCREMENT");
      }, 1000);
    },
  },
  getters: {
    evenOrOdd(state) {
      return state.count % 2 == 0 ? "偶数" : "奇数";
    },
  },
};
