<template>
  <div class="todo-header">
    <input type="text" placeholder="请输入你的任务名称，按回车键确认" @keyup.enter="addTodo" v-model="titleName" />
  </div>
</template>

<script>
export default {
  props: {},
  data() {
    return {
      titleName: ""
    };
  },
  methods: {
    addTodo() {
      const titleName = this.titleName.trim();
      if (!titleName) {
        return;
      }

      const todo = { title: titleName, complete: false };
      // this.AddTodo(todo);
      // 触发自定义事件
      // this.$emit("AddTodo", todo);
      this.$store.dispatch("demo2/AddTodo", todo);
      this.titleName = "";
    }
  }
};
</script>

<style>
.todo-header input {
  width: 560px;
  height: 28px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 4px 7px;
}

.todo-header input:focus {
  outline: none;
  border-color: rgba(82, 168, 236, 0.8);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075),
    0 0 8px rgba(82, 168, 236, 0.6);
}
</style>