<template>
  <div v-loading="loading">
    <p>{{$route.params.name}}</p>
    <ul>
      <li>name:{{msgdetail.name}}</li>
      <li>content:{{msgdetail.area}}</li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loading: true,
      msgdetail: {}
    };
  },
  mounted() {
    setTimeout(() => {
      const datas = [
        { name: "Arra", area: "a312sd" },
        { name: "ABArra", area: "asD1SDAd" },
        { name: "ACWrra", area: "aD213123WWsd" },
        { name: "ACrTRra", area: "aDWW12sd" },
        { name: "ACrra", area: "aD3123WWsd" },
        { name: "ACrEFra", area: "aDW313Wsd" },
        { name: "ACrraE", area: "32323332Wsd" },
        { name: "ACSrra", area: "1d" },
        { name: "ACDrra", area: "aDW321Wsd" },
        { name: "AVCrra", area: "aD123123WWsd" },
        { name: "ArDra", area: "asD3123213Dd" }
      ];
      this.datas = datas;
      const name = this.$route.params.name;
      this.msgdetail = datas.find(data => data.name == name);
      this.loading = false;
    }, 1000);
  },
  watch: {
    $route: function(value) {
      const name = value.params.name;
      this.msgdetail = this.datas.find(data => data.name == name);
    }
  }
};
</script>

