<template>
  <ul class="todo-main">
    <Item v-for="(todo,index) in todos" :key="index" :todo="todo" :index="index" />
  </ul>
</template>

<script>
import { mapState } from "vuex";
import Item from "./demo-2-todoItem";
export default {
  props: {
    // delTodo: Function,
    // todos: Array
  },
  computed: {
    ...mapState("demo2", ["todos"])
  },
  components: {
    Item
  },
  watch: {
    //监视
    todos: {
      deep: true, //深度监视
      handler: function(value) {
        // 将 todos 最新的值 的 json 数据 , 保存到 localStorage
        window.localStorage.setItem("todos_key", JSON.stringify(value));
      }
    }
  }
};
</script>

<style>
.todo-main {
  margin-left: 0px;
  border: 1px solid #ddd;
  border-radius: 2px;
  padding: 0px;
}

.todo-empty {
  height: 40px;
  line-height: 40px;
  border: 1px solid #ddd;
  border-radius: 2px;
  padding-left: 5px;
  margin-top: 10px;
}
</style>