<template>
  <div class="app-dd">
    <Nav />
    <Add :addComment="addComment" />
    <List :comments="comments" :deleteComment="deleteComment" />
  </div>
</template>

<script>
import Nav from "../components/demo1/demo-1-Nav";
import Add from "../components/demo1/demo-1-Add";
import List from "../components/demo1/demo-1-List";
export default {
  name: "Demo1",
  data() {
    return {
      comments: [
        {
          name: "a",
          content: "1"
        }
      ]
    };
  },
  components: {
    Nav,
    Add,
    List
  },
  methods: {
    addComment(comment) {
      this.comments.unshift(comment);
    },
    deleteComment(index) {
      this.comments.splice(index, 1);
    }
  }
};
</script>

<style>
.app-dd {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  /* grid-template-rows: 1fr; */
}
</style>