<template>
  <div class="add-1">
    <div class="add">
      <label class="cell-1-1">Name</label>
      <input type="text" class="cell-1-2" placeholder="Name" v-model="name" />
      <label class="cell-2-1">Content</label>
      <textarea rows="6" class="cell-2-2" placeholder="Content" v-model="content" />
      <button class="cell-3" type="button" @click.prevent="add()">submit</button>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    addComment: {
      type: Function,
      required: true
    }
  },
  data() {
    return {
      name: "",
      content: ""
    };
  },
  methods: {
    add() {
      // 检查数据输入的合法性
      const name = this.name.trim();
      const content = this.content.trim();
      if (!name || !content) {
        alert("Error !!!");
        return;
      }
      // 根据收集的数据封装成一个comment对象
      const comment = {
        name,
        content
      };
      // 添加到comments中
      this.addComment(comment);
      // 初始化
      this.name = "";
      this.content = "";
    }
  }
};
</script>

