<template>
  <li class="items">
    <div class="info">
      <div class="info-2">
        <div class="content-name">{{comment.name}}</div>
        <p>say:</p>
      </div>
      <p>{{comment.content}}</p>
    </div>

    <div class="del-btn">
      <button class="del" @click="delitem">DEL</button>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    comment: Object,
    deleteComment: Function,
    index: Number
  },
  methods: {
    delitem() {
      const { comment, deleteComment, index } = this;
      if (window.confirm(`Sure? delete ${comment.name} reply ?`)) {
        deleteComment(index);
      }
    }
  }
};
</script>

<style>
.items {
  padding: 15px;
  display: flex;
  flex-direction: row;
  font-size: 0.5em;
  list-style: none;
  border: 1px solid #eeeeee;
}

.content-name {
  font-size: 2em;
  margin-right: 5px;
}
.info {
  width: 100%;
}
.info-2 {
  display: flex;
  flex-direction: row;
  width: 70%;
}
.del-btn {
  align-self: flex-start;
}
.del {
  background-color: white; /* Green */
  color: #4caf50;
  border: none;
  cursor: pointer;
  outline: none;
  text-align: center;
  text-decoration: none;
  font-size: 12px;
  border-radius: 1px;
  transition: all 0.4s;
}
.del:hover {
  background-color: #4caf50; /* Green */
  color: white;
  box-shadow: 0 0px 1px 0 rgba(0, 0, 0, 0.24), 0 0px 1px 0 rgba(0, 0, 0, 0.19);
}
</style>