<template>
  <li class="demo2" @mouseenter="handleShow(true)" @mouseleave="handleShow(false)">
    <label>
      <input type="checkbox" v-model="todo.complete" />
      <span>{{ todo.title }}</span>
    </label>
    <div class="btn">
      <transition name="fade">
        <el-button class="bbtn" @click.prevent="deleteTodo" v-show="isShow" type="danger">删除</el-button>
      </transition>
    </div>
  </li>
</template>

<script>
// import PubSub from "pubsub-js";
export default {
  props: {
    // delTodo: Function,
    todo: Object,
    index: Number
  },
  data() {
    return {
      //   bgColor: "white",
      isShow: false
    };
  },
  methods: {
    handleShow(isEnter) {
      if (isEnter) {
        // this.bgColor = "#eee";
        this.isShow = true;
      } else {
        // this.bgColor = "white";
        this.isShow = false;
      }
    },
    deleteTodo() {
      const index = this;
      // console.log(todo.title);
      // delTodo(index);
      // // 发布消息
      // PubSub.publish("delTodo", index);

      this.$store.dispatch("demo2/delTodo", index);
    }
  }
};
</script>

<style>
.demo2 {
  list-style: none;
  height: 36px;
  line-height: 36px;
  padding: 5px 5px;
  border-bottom: 1px solid #ddd;
  transition: all 0.5s;
}
.demo2 label span {
  margin: 0 20px;
}

.demo2 label {
  float: left;
  cursor: pointer;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

.demo2 label li input {
  vertical-align: middle;
  margin-right: 6px;
  position: relative;
  top: -1px;
}
.btn {
  position: relative;
}
.bbtn {
  font-size: 10px;
  position: absolute;
  right: 5px;
}

/* .demo2 button {
  float: right;
  display: none;
  margin-top: 3px;
  transition: all 0.5s;
} */
li:hover.demo2 {
  background: #eeeeee;
}

li:before.demo2 {
  content: initial;
}

li:last-child.demo2 {
  border-bottom: none;
}
</style>
